div {

    .com {
        color: #93a1a1;
    }

    .lit {
        color: material-color('blue-grey', '900');;
    }

    .pun, .opn, .clo {
        color: #93a1a1;
    }

    .fun {
        color: #dc322f;
    }

    .str, .atv {
        color: material-color('orange', '800');;
    }

    .kwd, .prettyprint .tag {
        color: material-color('blue-grey', '900');;
    }

    .typ, .atn, .dec, .var {
        color: #3fbf79;
    }

    .pln {
        color: material-color('lime', '900');
    }

    .pun {
        color: material-color('lime', '900');
    }

    pre.prettyprint {
        padding: 15px;
        background-color: material-color('blue-grey', '50');
        border: 1px solid material-color('blue-grey', '100');
        border-radius: 0;
        margin-bottom: 30px;
        //font-size: 13px;
        font-family: $font-family-sans-serif;

        &.linenums {
            padding: 15px 5px;
        }
    }
    ol.linenums {
        margin: 0;

        li.L0, li.L1, li.L2, li.L3, li.L5, li.L6, li.L7, li.L8 {
            list-style-type: decimal;
        }
    }
    ol.linenums li {
        padding-left: 12px;
        color: material-color('blue-grey', '300');
        //color: #bebec5;
        line-height: 20px;
        //text-shadow: 0 1px 0 #fff;
    }
}