body {
    background-color: material-color('grey', '50');
    color: material-color('blue-grey', '700');
    padding-top: 52px;
}

h2, h3 {
    margin: 0 0 30px 0;
}
h4 {
    font-size: 22px;
    margin: 0 0 22px 0;
}
a {
    outline: none;
}
hr {
    border-color:material-color('grey', '400');
}

b, strong {
    color: #000;
}

.legend {
    font-size: 12px;
    margin-top: 0;
    color: forestgreen;
    user-select: none;
    font-weight: 700;
    white-space: pre-wrap;
}
table {
    border-collapse: separate;

    th:nth-of-type(1) {
        width: 24%;

    }
    th:nth-of-type(3) {
        width: 1%;
        white-space: nowrap;
    }
}

.parameter {
    display: inline-block;
    padding: 2px 10px;
    background-color: #eceff1;
    border: 1px solid #cfd8dc;
    margin-right: 10px;
    border-radius: 4px;
    color: #ef6c00;
    font-size: 18px;
}

.accent {
    display: inline-block;
    padding: 0 3px;
    background-color: #fff;
    border: 1px solid #cfd8dc;
    border-radius: 4px;
}

br[id] {
    font-size: 0;
    line-height: 0;
}

.nomargin {
    margin-bottom: 0 !important;
}

.vspace {
    margin-left: auto;
    margin-right: auto;
    max-width: 1060px;
}

.rspace {
    max-width: 1114px;
}

.lspace {
    max-width: 1114px;
    margin-left: 26px;
}