.site-header {
    background-color: material-color('blue-grey', '50');
    margin-bottom: 40px;
}
.menu--main {
    background-color: material-color('blue-grey', '600');
    margin: 0;
    padding: 0;
    border-color: material-color('blue-grey', '600');
    position: fixed;
    width: 100%;
    top: 0;
    left: 0;
    z-index: 15;

    .navbar-collapse {
        padding: 0;
    }

    &.navbar-inverse {
        .navbar-nav {
            > li {
                > a {
                    color: material-color('blue-grey', '100');

                    &:hover {
                        color: #fff;
                    }
                }

                &.active {
                    > a,
                    > a:hover,
                    > a:focus {
                        background-color: material-color('lime', '700');
                        color: #fff;
                    }
                }
            }
        }
    }
}
